<template>
  <v-container class="fill-height" >
      <v-row align="center" justify="center" class="mt-5" v-if="loading">
        <div class="text-center ma-12" >
          <v-progress-circular
            :size="150"
            class="mx-3"
            indeterminate
            color="primary"
          ></v-progress-circular>
        </div>
      </v-row>
      <v-row align="center" justify="center" class="mt-5">
        <v-col cols="12" v-bind:key="key" v-for="(app,key) in apps" lg="4" md="3" sm="12">
          <v-card
            v-if="!loading"
            class="mx-auto d-flex align-center justify-center pa-3 elevation-12"
            outlined
            :to="app.route"
            :color="app.color"
            dark
            style="height: 200px;"
          >
            <v-list-item three-line>
              <v-list-item-content>
                <div class="headline mb-1 text-center">{{app.title}}</div>
                <v-list-item-subtitle class="text-center mt-4">
                  <v-icon class="mx-2" large>{{app.icon}}</v-icon>
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
      </v-row>
    <!-- </div> -->
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      loading: null,
      apps: [
        {
          color: '#007bff',
          title: 'Monthly Reports',
          route: '/monthly-reports',
          icon: 'fas fa-chart-bar',
        },
        {
          color: '#dc3545',
          title: 'Classification Reports',
          route: '/classification-reports',
          icon: 'fas fa-chart-line',
        },
      ],
    };
  },
  mounted() {
    this.loading = true;
    if (!localStorage.isLoggedIn) {
      this.$router.push("/");
    }
    this.loading = false;
  },
};
</script>

